<template>
    <div class="find">
        <div class="findTop">
            <van-search
                v-model="searchvalue"
                show-action
                shape="round"
                placeholder="请输入搜索关键词"
                input-align="center"
                left-icon
            >
                <template #action>
                    <div class="personal" v-if="personalimg">
                        <img :src="personalimg" alt />
                    </div>
                    <div class="nologin" v-else @click="goLogin">
                        <img src="../../assets/img/nologin.svg" alt />
                    </div>
                </template>
                <template #left>
                    <div class="geren"></div>
                </template>
            </van-search>
        </div>
        <div class="findBody">
            <!-- 云村热评墙 -->
            <!-- <div class="hotWallIn" @click="goHotWord">
                <div class="bg"></div>
                <div class="hotWallInTop flexbetween">
                    <div class="hotWallInTop_left">
                        <span>云村热评墙</span>
                    </div>
                    <div class="hotWallInTop_right">
                        <span>{{ month }}.</span>
                    </div>
                </div>
                <div class="hotWallInBottom flexbetween">
                    <div class="hotWallInBottom_lfet">
                        <span>{{ userName }}，今日热评已更新，来围观吧！</span>
                    </div>
                    <div class="hotWallInBottom_right">
                        <span class="hotWallInBottom_day">{{ today }}</span>
                    </div>
                </div>
            </div> -->
            <!-- 独家放送 -->
            <div class="oneGet">
                <div class="oneGetTitle">新歌推荐</div>
                <div class="oneGetListBox">
                    <div
                        class="oneGetList"
                        :style="{ width: oneGetListWidth + 'px' }"
                    >
                        <div
                            class="oneGetListItem"
                            :style="{ width: itemWidth + 'px' }"
                            @touchstart="touchShotwordBody($event, index)"
                            @touchmove="touchMhotwordBody($event, index)"
                            @touchend="touchEhotwordBody($event, index)"
                            v-for="(item, index) in oneGetListData"
                            :key="index"
                        >
                            <div
                                class="oneGetListItem_item flexbetween"
                                v-for="(v, i) in oneGetListData[index]"
                                :key="i"
                            >
                                <div class="oneGetListItem_item_left">
                                    <div class="imgBox">
                                        <img :src="v.picUrl" alt />
                                    </div>
                                    <div class="title">
                                        <div class="songName one-ellipsis">{{
                                            v.name
                                        }}</div>
                                        <div class="singerName one-ellipsis">{{
                                            v.songAr
                                        }}</div>
                                    </div>
                                </div>
                                <div
                                    class="oneGetListItem_item_right"
                                    @click="clicksetUrl(v)"
                                >
                                    <img src="../../assets/img/play.svg" alt />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="findFoot"></div>
    </div>
</template>

<script>
import "../../assets/less/find.less";
import { createNamespacedHelpers } from "vuex";
import { setupCookie } from "../../assets/js/setupCookie";
const {
    mapState: musicState,
    mapActions: musicActions
} = createNamespacedHelpers("MusicAudioStore");
export default {
    data() {
        return {
            active: 0,
            //头像
            personalimg: "",
            //userName
            userName: "",
            // 搜索框
            searchvalue: "",
            //itemWidth小盒子宽度
            itemWidth: 0,
            //大盒子宽度
            oneGetListWidth: "",
            //数据
            oneGetListData: [],
            //按下位置
            startPagex: "",
            //松开位置
            endPagex: "",
            //按下标识
            touchBool: false,
            //日期
            month: "",
            today: ""
        };
    },
    computed: {
        ...musicState(["detailbool", "clickSong"])
    },
    created() {
        //获取用户登录状态
        this.getuserStatus();

        //独家放送列表
        this.getNewSong();
        // 设置宽度
        this.setItemWidth();
        //获取当前日期
        this.getDate();

        //设置音乐组件
        if (this.clickSong) {
            this.setaudioxy(0);
        }
    },
    methods: {
        ...musicActions([
            "setsongData",
            "seticonNum",
            "setaudioxy",
            "setClickSong"
        ]),
        //跳转到登录
        goLogin() {
            this.$router.push({ name: "Login" });
        },

        //获取用户登录状态
        getuserStatus() {
            //获取cookie
            let cookie = setupCookie.getCookies();
            if (cookie.split(";")[0] == "null") {
                this.personalimg = null;
                return;
            }
            this.axios({
                method: "GET",
                url: "/login/status",
                params: {
                    cookie
                }
            })
                .then(result => {
                    this.personalimg = result.data.profile.avatarUrl;
                    this.userName = result.data.profile.nickname;
                })
                .catch(err => {});
        },

        // 获取新音乐
        getNewSong() {
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "get",
                url: "/personalized/newsong",
                params: {}
            })
                .then(result => {
                    let data = result.data.result.slice(0, 9);
                    // 获取歌单的播放url
                    data.map((v, index) => {
                        //设置歌手名称
                        let songar = "";
                        v.song.artists.map(arv => {
                            songar += arv.name + "/";
                        });
                        v.songAr = songar.slice(0, -1);
                        //构造歌手名称给songdetail
                        v.ar = [];
                        v.ar.push({ name: v.song.artists[0].name });
                        //设置播放音乐链接
                        v.palyurl =
                            "https://music.163.com/song/media/outer/url?id=" +
                            v.id +
                            ".mp3";
                        //设置active
                        v.isActive = false;
                        //获取歌手专辑（获取歌手图片）
                        this.getSingerAlbum(v, v.song.artists[0].id);
                        //设置歌曲图片
                        let picUrlData = v.picUrl;
                        v.al = {};
                        v.al.picUrl = picUrlData;
                        //设置总时间
                        let m = Math.floor((v.song.duration / 1000 / 60) % 60);
                        let s = Math.floor((v.song.duration / 1000) % 60);
                        v.totalTime = m + "." + s;

                        //设置dt
                        let dtTime = v.song.duration;
                        v.dt = dtTime;
                        //设置歌词
                        //获取歌词
                        this.getSingerData(v, v.id);
                    });
                    let setData = [];
                    for (let i = 0; i < 3; i++) {
                        setData.push(data.slice(i * 3, i * 3 + 3));
                    }
                    this.oneGetListData = setData;
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //获取歌词
        getSingerData(v, id) {
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "get",
                url: "/lyric",
                params: {
                    id
                }
            })
                .then(result => {
                    //无歌词情况：1.纯音乐 2.无歌词源
                    if (Object.keys(result.data).indexOf("lrc") < 0) {
                        if (Object.keys(result.data).indexOf("nolyric") >= 0) {
                            //纯音乐 《纯纯的音乐》
                            let data = [
                                {
                                    nolyric: true,
                                    title: "纯音乐，请欣赏",
                                    isActive: false
                                }
                            ];
                            v.songWordData = data;
                        } else {
                            //无歌词源 《sca》
                            let data = [
                                {
                                    nolyric: true,
                                    title: "暂无歌词",
                                    isActive: false
                                }
                            ];
                            v.songWordData = data;
                        }
                    }
                    //有歌词情况：1.没有字 2.有字
                    else {
                        if (result.data.lrc.lyric == "") {
                            let data = [
                                {
                                    nolyric: true,
                                    title: "纯音乐，请欣赏",
                                    isActive: false
                                }
                            ];
                            v.songWordData = data;
                        } else {
                            let reg = "\\[.*\\]";
                            let sonArray = result.data.lrc.lyric.split("\n");

                            let data = [];
                            for (let i = 0; i < sonArray.length; i++) {
                                //没有包括[]
                                // 判断是否含有[00:00]格式
                                let timekey2 = "";
                                let timekey = "";
                                if (sonArray[i].indexOf(0) > -1) {
                                    //包括[]
                                    timekey = sonArray[i].match(reg)[0];
                                    timekey2 = sonArray[i].match(
                                        /[0-9]\d:[0-9]\d/
                                    )[0];
                                } else {
                                    timekey2 = "";
                                }
                                //拿到歌词文字
                                let timeLength = timekey.split("").length;
                                let songText = sonArray[i].slice(timeLength);
                                //歌词文字为空跳过该循环
                                if (songText == "") {
                                    continue;
                                }
                                data.push({
                                    time: timekey2,
                                    title: songText,
                                    isActive: false
                                });
                                v.songWordData = data;
                            }
                        }
                    }
                    this.$toast.clear();
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //获取歌手专辑
        getSingerAlbum(v, id) {
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "get",
                url: "/artist/album",
                params: {
                    id
                }
            })
                .then(result => {
                    this.$toast.clear();
                    v.singerImg = result.data.artist.picUrl;
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //点击设置音乐播放
        clicksetUrl(item) {
            //设置音乐组件位置
            this.setClickSong(true);
            this.setaudioxy(0);

            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            //传值到state
            this.setsongData(item);
            this.seticonNum(1);
        },

        //跳转到热评榜
        goHotWord() {
            this.$router.push({ name: "Hotword" });
        },

        //设置宽度
        setItemWidth() {
            this.itemWidth = document.body.clientWidth - 30;
        },

        //按下
        touchShotwordBody(e, index) {
            this.startPagex = "";
            this.startPagex = e.changedTouches[0].pageX;
            this.touchBool = true;
            let oneGetList = document.querySelector(".oneGetList");
            oneGetList.style.transitionDuration = "0s";
            this.oneGetListLeftNum = oneGetList.offsetLeft;
        },

        //移动
        touchMhotwordBody(e, index) {
            let movePagex = e.changedTouches[0].pageX;
            let oneGetList = document.querySelector(".oneGetList");

            let oneGetListLeft = "";
            if (movePagex - this.startPagex >= 0) {
                // 第一个不允许向右划
                if (this.oneGetListLeftNum == "0") {
                    return;
                }
                oneGetListLeft =
                    this.oneGetListLeftNum + movePagex - this.startPagex;
            } else {
                //设置无法左划
                if (index == this.oneGetListData.length - 1) {
                    return;
                }
                oneGetListLeft =
                    this.oneGetListLeftNum + movePagex - this.startPagex;
            }
            oneGetList.style.left = oneGetListLeft + "px";
        },

        //离开
        touchEhotwordBody(e, index) {
            this.endPagex = "";
            this.endPagex = e.changedTouches[0].pageX;
            this.touchBool = false;

            let chaNum = this.endPagex - this.startPagex;
            let oneGetList = document.querySelector(".oneGetList");

            oneGetList.style.transitionDuration = "0.3s";
            // 未达移动条件
            if (Math.abs(chaNum) < this.itemWidth / 2) {
                oneGetList.style.left = this.oneGetListLeftNum + "px";
            }
            //达到移动条件
            else {
                // 左划
                if (chaNum <= -this.itemWidth / 2) {
                    //设置无法左划
                    if (index == this.oneGetListData.length - 1) {
                        return;
                    }
                    oneGetList.style.left =
                        this.oneGetListLeftNum - this.itemWidth + "px";
                }
                //右划
                else if (chaNum >= this.itemWidth / 2) {
                    // 第一个不允许向右划
                    if (this.oneGetListLeftNum == "0") {
                        return;
                    }
                    oneGetList.style.left =
                        this.oneGetListLeftNum + this.itemWidth + "px";
                }
            }
        },

        //获取日期
        getDate() {
            let date = new Date();
            this.month = date.getMonth() + 1;
            this.month = this.month > 9 ? this.month : "0" + this.month;
            this.today = date.getDate();
        }
    }
};
</script>

<style lang="less" scoped>
</style>